@media only screen and (min-width: 800px) {
  .HomePage {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    background: #222222;
    color: #fff;
    /*@TODO: MAKE SURE TO UPDATE BOTH MOBILE AND DESKTOP CSS WHEN MAKING A UNIVERSAL UPDATE!!!!!!!!! */
    /* On smaller screens, decrease text size */
  }
  .HomePage .parent {
    position: relative;
    display: inline-block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .HomePage .parent .popup {
    visibility: hidden;
    width: 160px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 8px 0;
    position: absolute;
    z-index: 1;
    bottom: -75%;
    left: 50%;
    margin-left: -80px;
  }
  .HomePage .parent .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
  }
  .HomePage .colors {
    padding: 2px;
    background: #03020e;
    /* color: #022457; */
    font-size: 22px;
    margin-top: 0rem;
    display: inline-block;
    width: 100%;
    padding: 1px;
  }
  .HomePage .NoColors {
    background: #464545;
    font-size: 18px;
    margin-top: 2rem;
    display: inline-block;
    padding: 1px;
    width: 100%;
  }
  .HomePage .imgSrcs {
    width: 50%;
    height: auto;
  }
  .HomePage .thStyles {
    color: #fff;
  }
  .HomePage .mySlides {
    display: none;
  }
  .HomePage img {
    vertical-align: middle;
    max-width: 100%;
    height: auto;
  }
}
@media only screen and (min-width: 800px) and (max-width: 300px) {
  .HomePage .prev,
.HomePage .next,
.HomePage .text {
    font-size: 11px;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .redDiv {
    text-align: center;
    font-size: 2rem;
    color: black;
    background: white;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .columnStyle {
    -webkit-column-count: 2;
    column-count: 2;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .sponsors {
    text-align: center;
    padding: 5px;
    font-size: 30px;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .grid-container {
    display: grid;
    grid-gap: 0px;
    grid-template-columns: auto auto auto;
    padding: 15px;
    margin-right: 15px;
    margin-left: 15px;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .grid-item {
    /* background-color: rgba(255, 255, 255, 0.8); */
    /* border: 1px solid rgba(0, 0, 0, 0.8); */
    padding: 10px;
    font-size: 20px;
    text-align: center;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .linkSpo:hover {
    opacity: 0.85;
  }
}
@media only screen and (min-width: 800px) {
  .HomePage .div-container {
    margin: 0px;
  }
}
