.HomePage .alert {
  background: #2d2d2d;
  padding: 7px 9px;
  border: none;
  border-radius: 10px;
  width: 41%;
  margin-right: auto;
  margin-left: auto;
  color: white;
  cursor: pointer;
}
.HomePage .alertCovid {
  color: white;
  text-decoration: none;
}
.HomePage .alert:hover {
  opacity: 0.8;
}
