.RobotElement {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #222222;
  background: linear-gradient(to bottom right, #222 50%, red 50%);
  background-repeat: no-repeat;
  color: #fff;
}
@media only screen and (max-width: 799px) {
  .RobotElement .dropdown {
    display: none;
  }
}
.RobotElement h1 {
  text-align: center;
}
@media (min-width: 1490px) {
  .RobotElement table {
    position: relative;
    margin-right: auto;
    margin-left: auto;
  }
  .RobotElement .content {
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }
  .RobotElement .about {
    position: relative;
    font-size: 28px;
    margin-left: 10%;
    margin-right: 10%;
  }
}
@media (max-width: 1489px) {
  .RobotElement table {
    position: relative;
    margin-right: auto;
    margin-left: auto;
  }
  .RobotElement table td {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
    width: 100%;
  }
  .RobotElement .content {
    display: flex;
    margin: auto;
    flex-wrap: wrap-reverse;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }
  .RobotElement .about {
    position: relative;
    font-size: 28px;
    margin-left: 6%;
    margin-right: 6%;
  }
}
.RobotElement .infoBox1 {
  background: white;
  border-radius: 12px;
  color: #222;
}
.RobotElement .content {
  padding: 60px 90px;
  font-size: 20px;
  font-weight: 5200;
  text-align: left;
}
.RobotElement .headTxt {
  text-align: left;
  color: #4b4444;
  font-size: 30px;
}

