.About .page {
  background-color: #222;
}
@media only screen and (max-width: 799px) {
  .About .dropdown {
    display: none;
  }
}
.About .parent {
  position: relative;
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.About .parent .popup {
  visibility: hidden;
  width: 160px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  bottom: -75%;
  left: 50%;
  margin-left: -80px;
}
.About .parent .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}
.About .colors {
  background: #464545;
  font-size: 18px;
  margin-top: 12rem;
  display: inline-block;
  padding: 1px;
}
.About .NoColors {
  background: #464545;
  font-size: 18px;
  margin-top: 2rem;
  display: inline-block;
  padding: 1px;
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.About .imgSrcs {
  width: 50%;
  height: auto;
}
.About .thStyles {
  color: #fff;
}
.About .infoBox1 {
  background: white;
  border-radius: 12px;
}
.About .content {
  padding: 60px 90px;
  font-size: 20px;
  font-weight: 5200;
  text-align: left;
}
.About .headTxt {
  text-align: left;
  color: #4b4444;
  font-size: 30px;
}
@media (min-width: 1641px) {
  .About .words {
    display: inline-block;
    width: 50%;
  }
  .About .imgs img {
    display: inline-block;
    width: 70%;
  }
}
@media (max-width: 1640px) {
  .About .words {
    display: inline-block;
    width: 80%;
  }
  .About .imgs img {
    display: inline-block;
    width: 80%;
  }
}
.About .allCont {
  margin-left: 0px !important;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background: #222;
  background: linear-gradient(to bottom right, #222 50%, red 50%);
  color: #aaa;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}
.About .mainDiv {
  margin-right: 40px;
  margin-left: 40px;
}
.About .message {
  text-align: center;
  font-weight: 200;
  margin: 0.4em 0;
  font-size: 3.5em;
}
