.Outreach {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #222;
    color: #fff;
    width: 100%;
    position: relative;
    @media only screen and (max-width: 799px) {
        .dropdown {
            display: none;
        }
    }
    .parent {
        position: relative;
        display: inline-block;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    .parent .show {
        visibility: visible;
        -webkit-animation: fadeIn 1s;
        animation: fadeIn 1s;
    }
    @-webkit-keyframes fadeIn {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    @keyframes fadeIn {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    .AOSPac {
        padding: 2px;
        width: 100%;
        text-align: center;
        background: #000000;
        color: red;
        font-size: 15px;
        margin-top: 0.2rem;
        margin-bottom: 1.5rem;
        margin-left: auto;
        margin-right: auto;
    }
    .lists p {
        font-size: 21px;
    }
    .lists {
        height: auto;
        width: 100%;
        position: relative;
    }
    @media(min-width: 1635px) {
        .middle {
            position: relative;
            margin-left: 10rem;
            margin-right: 10rem;
        }
    }
    @media(max-width: 1285px) {
        .middle {
            position: relative;
            margin-left: 5rem;
            margin-right: 5rem;
        }
    }
    @media(max-width: 1085px) {
        .middle {
            position: relative;
            margin-left: 1rem;
            margin-right: 1rem;
        }
    }
    @media(max-width: 950px) {
        .middle {
            position: relative;
            margin-left: 0rem;
            margin-right: 0rem;
        }
        .middle img {
            width: 300px;
        }
        .middle p {
            font-size: 18px;
        }
    }
    .text {
        display: flex;
        flex-wrap: wrap;
    }
    .heading {
        font-size: 27px;
    }
    img {
        width: 60%;
    }
}