.VideoElement * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.VideoElement .v-header {
  height: 96vh;
  display: flex;
  align-items: center;
  color: black;
}
.VideoElement .container {
  max-width: 960px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: auto;
  text-align: center;
}
.VideoElement .fullscreen-video-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.VideoElement .fullscreen-video-wrap video {
  min-height: 100%;
  min-width: 100%;
}
.VideoElement .header-overlay {
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1;
  /* background: #225470; */
  /*blue*/
  /*background: rgb(207, 13, 13); */
  /*red*/
  background: #fff;
  /*white*/
  opacity: 0.7;
}
.VideoElement .header-content {
  z-index: 2;
}
.VideoElement .header-content h1 {
  font-size: 50px;
  margin-bottom: 0;
}
.VideoElement .header-content p {
  font-size: 1.5rem;
  display: block;
  padding-bottom: 2rem;
}
.VideoElement .btn {
  background: #000000;
  color: #fff;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  text-decoration: none;
  border-radius: 8px;
}
.VideoElement .btn:hover {
  opacity: 0.85;
}
.VideoElement .section {
  padding: 20px 0;
}
.VideoElement .section-b {
  background: #333;
  color: #fff;
}
@media (max-width: 960px) {
  .VideoElement .container {
    padding-right: 3rem;
    padding-left: 3rem;
  }
}
.VideoElement .banner,
.VideoElement .banner:after {
  -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
}
.VideoElement .banner {
  background: #0f4bcb;
  color: white;
  display: inline-block;
  height: auto;
  width: 140px;
  line-height: 1.2;
  margin: 0 0 40px;
  padding: 8px;
  position: relative;
  text-align: center;
  vertical-align: top;
  white-space: normal;
}
.VideoElement * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.VideoElement .text-center {
  text-align: center;
}
.VideoElement *:before,
.VideoElement *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.VideoElement .banner,
.VideoElement .banner:after {
  -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
}
.VideoElement .banner:after {
  content: "";
  position: absolute;
  display: block;
  border: 70px solid #0f4bcb;
  border-top-width: 5px;
  border-bottom: 20px transparent solid;
  top: 100%;
  left: 0;
}
.VideoElement *:before,
.VideoElement *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.VideoElement .animate {
  height: 100vh;
}
.VideoElement .secondSec {
  height: auto;
  overflow: auto;
}
.VideoElement .animate video {
  height: 100vh;
  width: 100%;
  object-fit: cover;
}
.VideoElement .introSec .texts {
  z-index: 2;
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  font-size: 70px;
  color: white;
  font-weight: 550 !important;
  opacity: 1 !important;
}
.VideoElement .secondSec .texts {
  z-index: 2;
  font-size: 70px;
  color: white;
  font-weight: 550 !important;
  opacity: 1 !important;
  margin-right: 30px;
  margin-left: 30px;
}
.VideoElement .secondSec .texts2 {
  z-index: 2;
  margin-top: 14px;
  font-size: 70px;
  color: white;
  font-weight: 550 !important;
  opacity: 1 !important;
  margin-right: 30px;
  margin-left: 30px;
}
.VideoElement .animate .texts .line {
  display: inline-block;
}
.VideoElement .animate .texts span {
  z-index: 2;
  font-size: 30px;
  color: white;
  font-weight: 500 !important;
  opacity: 1 !important;
}
.VideoElement section {
  display: none;
}
.VideoElement .infoTexts {
  font-size: 22.5px !important;
  padding: 10px;
  font-weight: 100;
}
.VideoElement .thirdSec .texts {
  float: right;
  text-align: right;
  z-index: 2;
  position: absolute;
  top: 0%;
  left: 5%;
  right: 5%;
  font-size: 60px;
  color: white;
  font-weight: 550 !important;
  opacity: 1 !important;
}
.VideoElement .colored {
  background: linear-gradient(to bottom, #ff8800, #ffe600);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
@keyframes changeColorToWhite {
  0% {
    color: transparent;
  }
  100% {
    color: white;
  }
}

