@media only screen and (min-width: 800px) {
  .Sponsors {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background: #222;
    color: #fff;
    /* On smaller screens, decrease text size */
  }
  .Sponsors .parent {
    position: relative;
    display: inline-block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .Sponsors .parent .popup {
    visibility: hidden;
    width: 160px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 8px 0;
    position: absolute;
    z-index: 1;
    bottom: -75%;
    left: 50%;
    margin-left: -80px;
  }
  .Sponsors .parent .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
  }
  .Sponsors .colors {
    padding: 2px;
    background: #222222;
    font-size: 22px;
    margin-top: 0rem;
    margin-bottom: 1rem;
    display: inline-block;
    padding: 1px;
  }
  .Sponsors .NoColors {
    background: #464545;
    font-size: 18px;
    margin-top: 2rem;
    display: inline-block;
    padding: 1px;
  }
  .Sponsors .imgSrcs {
    width: 50%;
    height: auto;
  }
  .Sponsors .thStyles {
    color: #fff;
  }
  .Sponsors .mySlides {
    display: none;
  }
  .Sponsors img {
    vertical-align: middle;
    max-width: 100%;
    height: auto;
  }
}
@media only screen and (min-width: 800px) and (max-width: 300px) {
  .Sponsors .prev,
.Sponsors .next,
.Sponsors .text {
    font-size: 11px;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .redDiv {
    text-align: center;
    font-size: 2rem;
    color: black;
    background: white;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .columnStyle {
    -webkit-column-count: 2;
    column-count: 2;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .sponsors {
    text-align: center;
    font-size: 30px;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .grid-container {
    display: grid;
    grid-gap: 0px;
    grid-template-columns: auto auto auto;
    padding: 15px;
    margin-right: 15px;
    margin-left: 15px;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .grid-item {
    /* background-color: rgba(255, 255, 255, 0.8); */
    /* border: 1px solid rgba(0, 0, 0, 0.8); */
    padding: 10px;
    font-size: 20px;
    text-align: center;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .linkSpo:hover {
    opacity: 0.85;
  }
}
@media only screen and (min-width: 800px) {
  .Sponsors .div-container {
    margin: 0px;
  }
}
