.VideoElement {
    * {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }
    .v-header {
        height: 96vh;
        display: flex;
        align-items: center;
        color: black;
    }
    .container {
        max-width: 960px;
        padding-left: 1rem;
        padding-right: 1rem;
        margin: auto;
        text-align: center;
    }
    .fullscreen-video-wrap {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        overflow: hidden;
    }
    .fullscreen-video-wrap video {
        min-height: 100%;
        min-width: 100%;
    }
    .header-overlay {
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        z-index: 1;
        /* background: #225470; */
        /*blue*/
        /*background: rgb(207, 13, 13); */
        /*red*/
        background: #fff;
        /*white*/
        opacity: 0.7;
    }
    .header-content {
        z-index: 2;
    }
    .header-content h1 {
        font-size: 50px;
        margin-bottom: 0;
    }
    .header-content p {
        font-size: 1.5rem;
        display: block;
        padding-bottom: 2rem;
    }
    .btn {
        background: #000000;
        color: #fff;
        font-size: 1.2rem;
        padding: 1rem 2rem;
        text-decoration: none;
        border-radius: 8px;
    }
    .btn:hover {
        opacity: 0.85;
    }
    .section {
        padding: 20px 0;
    }
    .section-b {
        background: #333;
        color: #fff;
    }
    @media(max-width:960px) {
        .container {
            padding-right: 3rem;
            padding-left: 3rem;
        }
    }
    .banner,
    .banner:after {
        -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
        filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
    }
    .banner {
        background: #0f4bcb;
        color: white;
        display: inline-block;
        height: auto;
        width: 140px;
        line-height: 1.2;
        margin: 0 0 40px;
        padding: 8px;
        position: relative;
        text-align: center;
        vertical-align: top;
        white-space: normal;
    }
    * {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .text-center {
        text-align: center;
    }
    *:before,
    *:after {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .banner,
    .banner:after {
        -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
        filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
    }
    .banner:after {
        content: '';
        position: absolute;
        display: block;
        border: 70px solid #0f4bcb;
        border-top-width: 5px;
        border-bottom: 20px transparent solid;
        top: 100%;
        left: 0;
    }
    *:before,
    *:after {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .animate {
        height: 100vh;
    }
    .secondSec {
        height: auto;
        overflow: auto;
    }
    .animate video {
        height: 100vh;
        width: 100%;
        object-fit: cover;
    }
    .introSec .texts {
        z-index: 2;
        position: absolute;
        top: 50vh;
        left: 50vw;
        transform: translate(-50%, -50%);
        font-size: 70px;
        color: white;
        font-weight: 550 !important;
        opacity: 1 !important;
    }
    .secondSec .texts {
        z-index: 2;
        font-size: 70px;
        color: white;
        font-weight: 550 !important;
        opacity: 1 !important;
        margin-right: 30px;
        margin-left: 30px;
    }
    .secondSec .texts2 {
        z-index: 2;
        margin-top: 14px;
        font-size: 70px;
        color: white;
        font-weight: 550 !important;
        opacity: 1 !important;
        margin-right: 30px;
        margin-left: 30px;
    }
    .animate .texts .line {
        display: inline-block;
    }
    .animate .texts span {
        z-index: 2;
        font-size: 30px;
        color: white;
        font-weight: 500 !important;
        opacity: 1 !important;
    }
    section {
        display: none;
    }
    .infoTexts {
        font-size: 22.5px !important;
        padding: 10px;
        font-weight: 100;
    }
    .thirdSec .texts {
        float: right;
        text-align: right;
        z-index: 2;
        position: absolute;
        top: 0%;
        left: 5%;
        right: 5%;
        font-size: 60px;
        color: white;
        font-weight: 550 !important;
        opacity: 1 !important;
    }
    .colored {
        background: linear-gradient(to bottom, rgb(255, 136, 0), rgb(255, 230, 0));
        color: transparent;
        -webkit-background-clip: text;
        background-clip: text;
    }
    @keyframes changeColorToWhite {
        0% {
            color: transparent;
        }
        100% {
            color: white;
        }
    }
}